.snip1226, .mobile-nav{
  font-family: 'Raleway', Arial, sans-serif;
  text-align: center;
  text-transform: uppercase;
  font-weight: 500;
  position: absolute;
  z-index: 99;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
}
.snip1226 * {
  box-sizing: border-box;
  -webkit-transition: all 0.35s ease;
  transition: all 0.35s ease;
}
.snip1226 div {
  list-style: outside none none;
  overflow: hidden;
}
.snip1226 a {
  padding: 0.3em 0;
  color: black;
  position: relative;
  display: inline-block;
  letter-spacing: 1px;
  margin: 0;
  text-decoration: none;
}
.snip1226 a:before,
.snip1226 a:after {
  position: absolute;
  -webkit-transition: all 0.35s ease;
  transition: all 0.35s ease;
  width: 100%;
}
.snip1226 a:before {
  bottom: 100%;
  display: block;
  height: 0.3vh;
  width: 100%;
  content: "";
  background-color: #e67e22;
}
.snip1226 a:after {
  padding: 0.3em 0;
  position: absolute;
  bottom: 100%;
  left: 0;
  content: attr(data-hover);
  color: black;
  white-space: nowrap;
}
.snip1226>div>div:hover>a,
.snip1226 .active{
  -webkit-transform: translateY(100%);
          transform: translateY(100%);
}
.image_with_tag {
  background-color: #000;
  color: #fff;
  display: inline-block;
  font-family: 'Roboto', sans-serif;
  font-size: 24px;
  margin: 10px;
  min-width: 230px;
  overflow: hidden;
  position: relative;
  text-align: center;
  width: 100%;
  height: 98vh;
  margin: 0px;
  margin-top: 2vh;
}

.image_with_tag * {
  box-sizing: border-box;
  -webkit-transition: all 0.45s ease;
  transition: all 0.45s ease;
}

.image_with_tag:before,
.image_with_tag:after {
  background-color: rgba(0, 0, 0, 0.5);
  border-top: 25vh solid rgba(0, 0, 0, 0.5);
  border-bottom: 25vh solid rgba(0, 0, 0, 0.5);
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  content: '';
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
  z-index: 1;
  opacity: 0;
}

.image_with_tag:before {
  -webkit-transform: scaleY(2);
  transform: scaleY(2);
}

.image_with_tag:after {
  -webkit-transform: scaleY(2);
  transform: scaleY(2);
}

.image_with_tag img {
  vertical-align: top;
  width: 100%;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}

.image_with_tag figcaption {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  align-items: center;
  z-index: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  line-height: 1.1em;
  opacity: 0;
  z-index: 2;
  -webkit-transition-delay: 0s;
  transition-delay: 0s;
}

.image_with_tag h3 {
  font-size: 1em;
  font-weight: 400;
  letter-spacing: 1px;
  margin: 0;
  text-transform: uppercase;
}

.image_with_tag h3 span {
  display: block;
  font-weight: 700;
}

.image_with_tag a {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 3;
}

.image_with_tag:hover > img,
.image_with_tag.hover > img {
  opacity: 0.7;
}

.image_with_tag:hover:before,
.image_with_tag.hover:before,
.image_with_tag:hover:after,
.image_with_tag.hover:after {
  -webkit-transform: scale(1);
  transform: scale(1);
  opacity: 1;
}

.image_with_tag:hover figcaption,
.image_with_tag.hover figcaption {
  opacity: 1;
  -webkit-transition-delay: 0.1s;
  transition-delay: 0.1s;
}



